.contact-form {
    width: 100%;
    margin-top: 20px;
  
    ul {
      padding: 0;
      margin: 0;
  
      li {
        padding: 0;
        margin: 0;
        list-style: none;
        margin-bottom: 10px;
        opacity: 0;
        overflow: hidden;
        display: block;
        clear: both;
        position: relative;
        animation: fadeInUp 2s 2s;
        animation-fill-mode: forwards;
      }
  
      li.half {
        width: 49%;
        margin-left: 2%;
        float: left;
        clear: none;
  
        &:first-child {
          margin-left: 0;
        }
      }
    }
  
    input[type='text'],
    input[type='email'] {
      width: 100%;
      border: 0;
      background: #115173;
      height: 50px;
      font-size: 16px;
      color: #fff;
      padding: 0 20px;
      box-sizing: border-box;
    }
  
    textarea {
      width: 100%;
      border: 0;
      background: #115173;
      height: 50px;
      font-size: 16px;
      color: #fff;
      padding: 20px;
      box-sizing: border-box;
      min-height: 150px;
    }
  
    .flat-button { /* Make the animation when the pointer arrives there*/
      color: #00ffff;
      font-size: 11px;
      letter-spacing: 3px;
      font-family: sans-serif;
      padding: 8px 10px;
      border: 1px solid #00ffff;
      float: left;
      border-radius: 4px;
      background: 0 0;
      text-transform: uppercase;
      float: right;
      text-align: center;
      margin-right: 10px;
      cursor: pointer;

    }

    .flat-button:hover { /*Copy this and modify colors to contact and about me buttons*/
      transform: translateY(-3px);
      background: #00ffff;
      color: #000;
    }
}

.info-map {
    position: absolute;
    background: #000;
    bottom: 50px; /*This is the position of black frame when you see the address, before was TOP: 50px*/
    right: 30%;
    z-index: 999999;
    width: 250px;
    padding: 20px;
    color: #fff;
    font-family: 'Helvetica';
    font-size: 17px;
    font-weight: 300;
    opacity: 0;
    animation: fadeIn 1s 1.5s;
    animation-fill-mode: forwards;
  
    span {
        font-size: 16px;
        display: block;
        padding-top: 20px;
        color: #00ffff;
    }
}

.map-wrap {
    background: rgba(8, 253, 216, 0.1);
    float: right;
    width: 53%;
    height: 100%;
}

.leaflet-container {
    width: 100%;
    height: 100%;
    position: relative;
    opacity: 0;
    animation: backInRight 1s 1.2s;
    animation-fill-mode: forwards;
}

@media screen and (max-width: 1200px) {
  .map-wrap {
    float: none;
    width: 100%;
    height: 400px;
    margin: 0;
  }

  .info-map {
    display: none; 
  }
}